* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;

  /* background-color: #818cf8; */
  background-color: #0f172a;

  /* background-color: transparent; */
}

@font-face {
  font-family: "Kandinsky";
  src: url("./fonts/kandinsky.ttf") format("opentype");
  /* font-weight: bold; */
}

@font-face {
  font-family: "Passeggero";
  src: url("./fonts/Passeggero.otf") format("opentype");
}

@font-face {
  font-family: "Emirates";
  src: url("./fonts/Emirates.ttf") format("opentype");
}

/* HEADER COMPONENTS*/

header {
  background-image: linear-gradient(180deg, #1e293b, #0f172a);
  height: 200px;
  position: fixed;
  width: 100%;
  z-index: 1;
  /* mix-blend-mode: difference; */
}

.navbar {
  background-color: transparent;
  padding: 20px;
  margin: 50px;
}

/* nav a:hover {
  color: #14b8a6;
  transition: 0.8s;
} */

.navbar-ul {
  background-color: transparent;
  width: 100%;
  display: flex;
  justify-content: space-around;
}

.navbar-ul li {
  list-style: none;
  background-color: transparent;
}

.navbar-ul li a {
  color: white;
  /* color: #dc2626; */
  font-size: 2rem;
  font-family: "Marcellus", sans-serif;
  text-decoration: none;
  background-color: transparent;
  font-weight: bold;

  /* display: grid;
  grid-template-columns: 2fr, repeat(5, 1fr); */
}

/* MAIN COMPONENTS*/

main {
  /* background-color: #fef08a; */
  margin: 0px 50px;
  position: relative;
  padding-top: 200px;
  /* padding-top: 150px; */
}

section {
  min-height: 100vh;
}

.menu-options {
}

#home {
  display: flex;
  /* justify-content: center; */
  background-color: #0f172a;
  height: 90vh; /*?????*/
}
#home-left {
  background-color: transparent;
  width: 50%;
  display: flex;
  align-items: center;
  padding: 0 30px;
}
#home-right {
  /* background-color: #ef4444; */
  background-image: url("../public/images/kyrgyz.jpg");
  background-size: cover;
  background-position: center;
  display: flex;
  width: 40%;
  justify-content: center;
}

#parallax {
  margin: 50px;
  color: #ef4444;
  font-size: 60px;
  /* background-image: url("../public/images/kyrgyz.jpg"); */
  /* background-color: #ef4444; */
}

#home h1 {
  background-color: transparent;
  color: #f8fafc;
  /* font-size: 140px; */
  font-size: 5vw;
  /* font-family: "Monoton", sans-serif; */
  font-family: "Kandinsky", sans-serif;

  color: transparent;
  text-align: center;
  transition: color 2s;
  background: url("../public/images/nature1.jpg") no-repeat;
  background-position: center center;
  /* background-position-x: 20px;
  background-position-y: 20px; */

  background-size: cover;
  background-clip: text;
  -webkit-background-clip: text;
  /* font-weight: bold; */
  /* font-style: italic; */
  font-weight: 400;
  /* letter-spacing: 5px; */
}

#home h1:hover {
  color: #fcbf1e;
}

.text-block {
  height: 100%;
  max-width: 1000px;
  margin: auto;
  padding: 20px 100px;
  background-color: transparent;
}

h2 {
  /* font-family: "Proza Libre", sans-serif; */
  /* font-family: "Libre Barcode 39 Extended Text", sans-serif; */
  font-family: "Passeggero", sans-serif;
  font-size: 60px;
  text-align: center;
  padding: 20px 0px;
  background-color: transparent;
  padding-top: 80px;
  opacity: 70%;
}

h3 {
  font-family: "Emirates", sans-serif;
  font-size: 20px;
  font-weight: 800;
  line-height: 2;
  text-align: justify;
  background-color: transparent;
  padding-top: 50px;
  color: #120136;
  /* font-style: italic; */
}

.author-image {
  display: block;
  margin: 50px auto 30px;
  max-height: 400px;
  max-width: 400px;
  background-color: #035aa6;
  border-radius: 200px;
  border: 10px solid #fcbf1e;
}

.author-image:hover {
  transition: 1s;
  transform: scale(110%);
}

/*  About me*/

#about-me {
  /* background-image: linear-gradient(180deg, #035aa6, #40bad5); */
}

#about-me .text-block {
  /* background-image: linear-gradient(180deg, #035aa6, #40bad5); */
}

/* Projects */

#projects {
  background-color: 171717;
}

#projects .text-block {
  /* background-color: #40bad5; */
}

.project-container {
  background-color: transparent;
  margin: 0 auto;
}

.project-block {
  text-align: center;
  background-color: transparent;
}

.project-block h3 {
  padding: 40px 0px 10px;
  text-align: center;
  font-weight: 700;
  font-size: 28px;
}

.project-block p {
  background-color: transparent;
  text-align: justify;
  font-family: "Passeggero", sans-serif;
  font-size: 20px;
  padding: 10px 0px;
}

.project-link {
  background-color: green;
  display: inline-block;
  border-radius: 25px;
  line-height: 0;
}

.project-image {
  height: 400px;
  width: 750px;
  background-color: transparent;
  border-radius: 25px;
  border: 8px solid #fcbf1e;
  object-fit: contain;
  width: 100%;
}

.project-image:hover {
  color: #f43f5e;
  transition: 0.8s;
  transform: scale(110%);
  margin-top: 20px;
}

/* Services */

.block-container {
  background-color: transparent;
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  align-content: space-between;
  padding-top: 100px;
}

.blocks {
  height: 220px;
  width: 220px;
  display: flex;
  justify-content: center;
  align-items: center;
  align-content: space-between;

  background-color: transparent;
  border-radius: 10px;
  margin: 10px;
}

.blocks:hover {
  background-color: #fcbf1e;
  transition: 0.5s;
  border: 10px solid #120136;
}

.block-image {
  background-color: transparent;
  height: 150px;
  width: 150px;
  display: flex;
}

/* Contact Me */

#contact-me {
  background-image: linear-gradient(180deg, #035aa6, #120136);
}

#contact-me .text-block {
  /* background-image: linear-gradient(180deg, #035aa6, #120136); */
}

.contact-form {
  height: 800px;
  margin: 50px auto;
  display: flex;
  align-items: center;
  flex-flow: column wrap;
  justify-content: space-between;
  background-color: transparent;
}

.contact-block {
  height: 120px;
  width: 120px;
  background-color: #120136;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 6px solid #035aa6;
  border-radius: 30px;
}

.contact-block:hover {
  background-color: #fcbf1e;
  border: 8px solid #120136;
  transition: 1s;
}

.contact-image {
  height: 60px;
  width: 60px;
  background-color: transparent;
}

/* FOOTER COMPONENTS*/

.footer {
  background-color: #ef4444;
  color: salmon;
  margin: 0px 50px;
  font-family: "Catamaran", sans-serif;
  font-size: 45px;
  text-align: center;
  /* padding: 45px; */
  width: calc(100% - 100px);
  position: relative;
  margin-bottom: 0;
  z-index: 3;
}
